.header {
  backdrop-filter: blur(5px);
  z-index: 1000;
  background: #12121266;
  justify-content: space-between;
  align-items: center;
  padding: 1rem 2rem;
  transition: transform .3s, opacity .3s;
  display: flex;
  position: fixed;
  top: 0;
  left: 0;
  right: 0;
}

.header.visible {
  opacity: 1;
  transform: translateY(0);
}

.header.hidden {
  opacity: 0;
  transform: translateY(-100%);
}

.header-logo {
  height: 50px;
}

.nav-links {
  gap: 1.5rem;
  display: flex;
}

.nav-link {
  color: #faf9f6;
  font-size: 1rem;
  font-weight: 700;
  transition: color .3s, opacity .3s;
}

.nav-link:hover {
  color: #3498db;
}

.menu-toggle {
  color: #faf9f6;
  cursor: pointer;
  background: none;
  border: none;
  font-size: 2rem;
  display: none;
}

@media (width <= 768px) {
  .menu-toggle {
    display: block;
  }

  .nav-links {
    background: #121212e6;
    border-radius: 0 0 0 8px;
    flex-direction: column;
    width: 80%;
    max-width: 300px;
    padding: 1rem;
    display: none;
    position: fixed;
    top: 70px;
    right: 0;
    box-shadow: -2px 0 8px #0000004d;
  }

  .nav-links.open {
    display: flex;
  }

  .nav-links .nav-link {
    width: 100%;
    padding: .5rem 0;
  }
}

.hero-section {
  opacity: 0 !important;
  width: 100% !important;
  height: 80vh !important;
  margin-top: 0 !important;
  padding-top: 0 !important;
  transition: all .8s ease-out !important;
  position: relative !important;
  overflow: hidden !important;
  transform: translateY(20px) !important;
}

.hero-section.visible {
  opacity: 1 !important;
  transform: translateY(0) !important;
}

.hero-section .carousel .slide {
  height: 80vh !important;
  position: relative !important;
}

.hero-section .carousel .slide:after {
  content: "" !important;
  z-index: 1 !important;
  background: #0006 !important;
  width: 100% !important;
  height: 100% !important;
  position: absolute !important;
  top: 0 !important;
  left: 0 !important;
}

.hero-section .carousel .slide img {
  object-fit: cover !important;
  width: 100% !important;
  height: 100% !important;
}

.hero-section .carousel .control-arrow {
  opacity: 0 !important;
  background: none !important;
  height: auto !important;
  padding: 20px !important;
  transition: opacity .3s !important;
  top: 50% !important;
  transform: translateY(-50%) !important;
}

.hero-section .carousel .control-arrow:hover {
  background: none !important;
}

.hero-section .carousel:hover .control-arrow {
  opacity: .8 !important;
}

.hero-section .carousel .control-arrow:before {
  border-width: 3px !important;
  border-color: #fff !important;
}

.hero-section .carousel .control-prev.control-arrow:before {
  border-bottom: 3px solid #fff !important;
  border-right: 3px solid #fff !important;
}

.hero-section .carousel .control-next.control-arrow:before {
  border-bottom: 3px solid #fff !important;
  border-left: 3px solid #fff !important;
}

.hero-section .carousel .control-dots {
  opacity: 1 !important;
  margin: 0 !important;
  padding: 0 !important;
  bottom: 20px !important;
}

.hero-section .carousel .dot {
  width: 12px !important;
  height: 12px !important;
  box-shadow: none !important;
  background: #fffc !important;
  margin: 0 8px !important;
}

.hero-section .carousel .dot.selected {
  background: #fff !important;
}

.hero-section .carousel .legend {
  color: #faf9f6 !important;
  text-align: center !important;
  box-sizing: border-box !important;
  opacity: 1 !important;
  z-index: 2 !important;
  text-shadow: 2px 2px 4px #00000080 !important;
  background: #d3d3d300 !important;
  border-radius: 15px !important;
  width: 80% !important;
  max-width: 1200px !important;
  margin: 0 auto !important;
  padding: 30px !important;
  font-size: 3rem !important;
  font-weight: 700 !important;
  position: absolute !important;
  bottom: 40px !important;
  left: 0 !important;
  right: 0 !important;
}

@media (width <= 768px) {
  .hero-section, .hero-section .carousel .slide {
    height: 60vh !important;
  }

  .hero-section .carousel .legend {
    width: 90% !important;
    padding: 20px !important;
    font-size: 2rem !important;
    bottom: 20px !important;
  }
}

@media (width <= 480px) {
  .hero-section, .hero-section .carousel .slide {
    height: 50vh !important;
  }

  .hero-section .carousel .legend {
    width: 95% !important;
    padding: 10px !important;
    font-size: 1.5rem !important;
    bottom: 10px !important;
  }
}

.about-section {
  background: url("tpsiteSkylineStock3.aa679205.jpg") center / cover no-repeat fixed;
  justify-content: center;
  align-items: center;
  width: 100%;
  min-height: 100vh;
  transition: opacity .5s ease-in-out;
  display: flex;
  position: relative;
}

.about-section:after {
  content: "";
  z-index: 1;
  background: radial-gradient(circle, #000c 0%, #0006 50%, #000c 100%);
  position: absolute;
  inset: 0;
}

.about-content {
  z-index: 2;
  text-align: center;
  width: 90%;
  max-width: 800px;
  padding: 2rem;
  position: relative;
}

@media (width <= 768px) {
  .about-section {
    background-attachment: scroll;
    padding: 2rem 1rem;
  }

  .about-content {
    padding: 1rem;
  }
}

@media (width <= 480px) {
  .about-content {
    padding: .5rem;
  }
}

.team-section {
  background-color: #101214;
  padding: 4rem 10% 8rem;
  position: relative;
  overflow: hidden;
}

.team-section:before {
  content: "";
  opacity: .05;
  z-index: 1;
  background: url("motion-background.6277a59a.jpg") center / cover no-repeat;
  position: absolute;
  inset: 0;
}

.team-content {
  z-index: 2;
  text-align: center;
  position: relative;
}

.subtitle {
  color: #d3d3d3;
  opacity: .55;
  margin: -1rem 0 3rem;
  font-size: 1.5rem;
  font-weight: 400;
}

.team-grid {
  grid-template-columns: repeat(5, 1fr);
  justify-items: center;
  gap: 2rem;
  max-width: 1200px;
  margin: 0 auto 4rem;
  display: grid;
}

.team-member {
  opacity: 0;
  flex-direction: column;
  align-items: center;
  transition: opacity .6s ease-out, transform .6s ease-out;
  display: flex;
  transform: translateY(20px);
}

.team-member.visible {
  opacity: 1;
  transform: translateY(0);
}

.member-image-container {
  border: 3px solid #000;
  border-radius: 50%;
  width: 200px;
  height: 200px;
  margin-bottom: 1rem;
  transition: transform .3s ease-out, box-shadow .3s ease-out;
  overflow: hidden;
}

.member-image-container:hover {
  transform: scale(1.03);
  box-shadow: 0 6px 16px #00000040;
}

.member-image {
  object-fit: cover;
  width: 100%;
  height: 100%;
}

@media (width <= 1024px) {
  .team-grid {
    grid-template-columns: repeat(3, 1fr);
  }

  .member-image-container {
    width: 150px;
    height: 150px;
  }
}

@media (width <= 768px) {
  .team-section {
    padding: 3rem 5% 6rem;
  }

  .team-grid {
    grid-template-columns: repeat(2, 1fr);
  }

  .member-image-container {
    width: 120px;
    height: 120px;
  }
}

@media (width <= 480px) {
  .team-section {
    padding: 2rem 2% 4rem;
  }

  .team-grid {
    grid-template-columns: 1fr;
  }

  .member-image-container {
    width: 100px;
    height: 100px;
  }
}

.contact-section {
  background: url("tpsiteSpaceStock3.fe2357bf.jpg") center / cover no-repeat fixed;
  justify-content: center;
  align-items: center;
  width: 100%;
  min-height: 100vh;
  transition: opacity .5s ease-in-out;
  display: flex;
  position: relative;
}

.contact-section:after {
  content: "";
  z-index: 1;
  background: radial-gradient(circle, #000c 0%, #0009 50%, #000c 100%);
  position: absolute;
  inset: 0;
}

.contact-content {
  z-index: 2;
  background: #000000b3;
  border-radius: 10px;
  width: 90%;
  max-width: 600px;
  padding: 3rem;
  position: relative;
  box-shadow: 0 4px 6px #0000001a;
}

.contact-form {
  flex-direction: column;
  gap: 1.5rem;
  width: 100%;
  margin-top: 2rem;
  display: flex;
}

.form-group input, .form-group textarea {
  color: #faf9f6;
  box-sizing: border-box;
  background: #121212f2;
  border: 1px solid #ffffff1a;
  border-radius: 4px;
  width: 100%;
  padding: 1rem;
  transition: border-color .3s, background .3s;
}

.submit-btn {
  color: #fff;
  cursor: pointer;
  text-align: center;
  background: #3498db;
  border: none;
  border-radius: 4px;
  width: 100%;
  padding: 1rem;
  font-weight: 600;
  transition: background .3s;
}

.submit-btn:hover {
  background: #2980b9;
}

@media (width <= 768px) {
  .contact-section {
    background-attachment: scroll;
    padding: 2rem 1rem;
  }

  .contact-content {
    padding: 2rem 1rem;
  }
}

@media (width <= 480px) {
  .contact-content {
    padding: 1rem .5rem;
  }
}

.apply-section {
  background: url("tpsiteSkylineStock5.541e3964.jpg") center / cover no-repeat fixed;
  padding-top: 8rem;
  scroll-margin-top: 8rem;
  position: relative;
  overflow: hidden;
}

.apply-section:before {
  content: "";
  -webkit-backdrop-filter: blur(8px);
  z-index: 1;
  background: #0006;
  position: absolute;
  inset: 0;
}

.section-overlay {
  z-index: 2;
  height: 150px;
  position: absolute;
  left: 0;
  right: 0;
}

.apply-content {
  z-index: 3;
  text-align: center;
  max-width: 1200px;
  margin: 0 auto;
  padding: 2rem;
  position: relative;
}

.apply-content h2 {
  color: #faf9f6;
  margin-bottom: 2rem;
  font-size: clamp(2rem, 5vw, 3rem);
}

.benefits-grid {
  grid-template-columns: repeat(auto-fit, minmax(250px, 1fr));
  gap: 2rem;
  display: grid;
}

.benefit-card {
  aspect-ratio: 1;
  background: #ffffff0d;
  border-radius: 12px;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  padding: 1.5rem;
  transition: transform .3s, background .3s;
  display: flex;
  position: relative;
}

.benefit-card:hover {
  background: #ffffff14;
  transform: translateY(-8px);
}

.benefit-icon {
  color: #3498db;
  margin-bottom: 1rem;
  font-size: 2.5rem;
}

.benefit-card h3 {
  color: #faf9f6;
  margin-bottom: .75rem;
  font-size: 1.25rem;
}

.benefit-card p {
  color: #a0a0a0;
  text-align: center;
  font-size: 1rem;
  line-height: 1.4;
}

.interview-section {
  z-index: 3;
  text-align: center;
  padding: 4rem 2rem 6rem;
  position: relative;
}

.application-section h2 {
  color: #faf9f6;
  margin-bottom: .5rem;
  font-size: clamp(1.75rem, 4vw, 2.5rem);
}

.application-section .subtitle {
  color: #a0a0a0;
  margin-bottom: 2rem;
  font-size: clamp(1rem, 2.5vw, 1.25rem);
}

.application-form {
  gap: 1rem;
  max-width: 600px;
  margin: 0 auto;
  display: grid;
}

.application-form .form-group input, .application-form .form-group select, .application-form .form-group textarea {
  color: #faf9f6;
  resize: vertical;
  background: #1a1a1a;
  border: none;
  border-radius: 6px;
  width: 100%;
  padding: 1rem;
  font-family: inherit;
  font-size: 1rem;
  transition: background .3s;
}

.application-form .form-group input:focus, .application-form .form-group select:focus, .application-form .form-group textarea:focus {
  background: #ffffff1a;
  outline: none;
}

.file-upload {
  color: #faf9f6;
  cursor: pointer;
  background: #1a1a1a;
  border: 1px solid #0000;
  border-radius: 6px;
  padding: 1rem;
  transition: background .3s, border-color .3s;
  display: inline-block;
  position: relative;
}

.file-upload input[type="file"] {
  opacity: 0;
  cursor: pointer;
  width: 100%;
  height: 100%;
  position: absolute;
  top: 0;
  left: 0;
}

.file-upload span {
  pointer-events: none;
}

.file-upload:hover {
  background: #3498db26 !important;
  border-color: #3498db !important;
}

.file-upload:hover span {
  color: #3498db !important;
}

.file-upload.selected {
  background: #3498db1a !important;
  border-color: #3498db !important;
}

.file-upload.selected span {
  color: #3498db !important;
}

.submit-btn {
  cursor: pointer;
  border-radius: 6px;
  margin-top: 1.5rem;
  padding: 1rem;
  font-size: 1rem;
  font-weight: 700;
  transition: background .3s, border-color .3s, color .3s;
  color: #faf9f6 !important;
  background: #1a1a1a !important;
  border: 1px solid #0000 !important;
}

.submit-btn:hover {
  color: #3498db !important;
  background: #3498db26 !important;
  border-color: #3498db !important;
}

@media (width <= 768px) {
  .apply-content, .interview-section {
    padding: 2rem 1rem;
  }

  .benefits-grid {
    grid-template-columns: 1fr;
  }
}

